mily=Prompt:wght@200;300;400;500;600;700;800;900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300;1,400&display=swap');\r\n\r\n@mixin AllButtons {\r\n    font-size: 20px;\r\n    font-weight: 500;\r\n    font-family: 'Prompt', sans-serif;\r\n    background: rgb(109, 160, 168);\r\n    color: rgb(238, 238, 238);\r\n    // border: 0;\r\n    border-radius: 5px;\r\n}\r\n\r\n@mixin InputsText {\r\n    padding: 0px;\r\n    margin: 0px;\r\n    font-style: italic;\r\n}\r\n\r\nbody {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    background: rgb(238, 238, 238);\r\n    font-family: 'Prompt', sans-serif;\r\n}\r\n\r\n#app {\r\n    display: flex;\r\n    justify-content: center;\r\n}\r\n\r\n.App {\r\n    width: 500px;\r\n    height: 700px;\r\n    margin-top: 20px;\r\n    border: 1px solid rgb(109, 160, 168);\r\n    background: no-repeat center/100% url(\"./img/Water.png\");\r\n    border-radius: 5px;\r\n    box-shadow: 0 0 10px rgba(0,0,0,0.5);\r\n}\r\n\r\n.Title {\r\n    margin: 0px;\r\n    padding: 15px;\r\n    background: rgb(109, 160, 168);\r\n    color: rgb(255, 255, 255);\r\n}\r\n\r\n.ControlsBar {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    flex-flow: wrap;\r\n    margin: 5px;\r\n    \r\n}\r\n\r\n.ControlsBarInput {\r\n    padding: 10px;\r\n    width: 100%;\r\n    font-family: 'Prompt', sans-serif;\r\n    font-size: 20px;\r\n    font-weight: 400;\r\n}\r\n\r\n.ControlsBarButtons {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    width: 100%;\r\n    margin: 5px 0;\r\n}\r\n\r\n.ControlsBarButtonsNotActive {\r\n    height: 50px;\r\n    width: 33%;\r\n    @include AllButtons;\r\n}\r\n\r\n.ControlsBarButtonsActive {\r\n    height: 50px;\r\n    width: 33%;\r\n    @include AllButtons;\r\n    background: rgb(14, 187, 255);\r\n}\r\n\r\n.ControlsBar hr {\r\n    width: 100%;\r\n    height: 1px;\r\n    background: rgb(109, 160, 168);\r\n}\r\n\r\n.TaskList {\r\n    margin: 5px;\r\n    height: 300px;\r\n    overflow-y: auto;\r\n}\r\n\r\n.TaskItem {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    background: rgb(238, 238, 238);\r\n    margin-bottom: 10px;\r\n    border-bottom: 1px solid rgb(173, 173, 173);\r\n    border-left: 1px solid rgb(173, 173, 173);\r\n}\r\n\r\n.TaskItemValues p{\r\n    margin: 0;\r\n    padding: 0 5px;\r\n    color: rgb(109, 160, 168);\r\n    font-family: 'Roboto', sans-serif;\r\n    font-weight: 600;\r\n}\r\n\r\n.TasksButtonBlock {\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n\r\n.TasksButton {\r\n    height: 70%;\r\n    margin-left: 2px; \r\n    @include AllButtons;\r\n}\r\n\r\n.NotActiveTask {\r\n    height: 70%;\r\n    margin-left: 2px; \r\n    @include AllButtons;\r\n    background: rgb(224, 100, 100);\r\n}\r\n\r\n.Form {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    flex-flow: wrap;\r\n    margin: 5px;\r\n}\r\n\r\n.FormInput {\r\n    padding: 8px;\r\n    width: 100%;\r\n    font-family: 'Prompt', sans-serif;\r\n    font-size: 13px;\r\n    font-weight: 400;\r\n}\r\n\r\n.enterTaskName {\r\n    @include InputsText\r\n}\r\n\r\n.selectImportance {\r\n    @include InputsText\r\n}\r\n\r\n.addTaskButton {\r\n    width: 100%;\r\n    margin-top: 5px;\r\n}\r\n\r\n.addTaskButton:disabled {\r\n    font-size: 20px;\r\n    font-weight: 400;\r\n    font-family: 'Prompt', sans-serif;\r\n    background: rgb(197, 197, 197);\r\n    color: rgb(0, 0, 0);\r\n    // border: 0;\r\n    border-radius: 5px;\r\n}\r\n\r\n.addTaskButton:enabled {\r\n    @include AllButtons\r\n}"],"sourceRoot":""}]);
